.wrapper {
  width: 100%;
  height: auto;
  background-color: #282c34;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  position: relative;
  z-index: 10;
}

.portfolioContainer {
  position: relative;
  justify-content: center;
  align-content: center;
  text-align: center;
}

.portfolioRow {
  position: relative;
  justify-content: center;
  align-content: center;
  text-align: center;
}

.portfolioHeading {
  color: #ffffff;
}

.headingHr {
  background-color: #61dafb;
  width: 13%;
}

.cardRow {
  position: relative;
  justify-content: center;
  text-align: center;
  align-content: center;
}

.col-lg-4 {
  flex: 33%;
  max-width: 33%;
}

#foreCast,
#similTune,
#jBot,
#triviaGame,
#boxes,
#confetti,
#globe {
  vertical-align: middle;
  width: 100%;
  height: 100%;
}
